<template>
  <div class="a">
    
    <div>
      <yd-cell-group>
        <yd-cell-item arrow>
          <span slot="left"></span>
          <input slot="right" type="text" @click.stop="show1 = true" v-model="model1" readonly placeholder="">
        </yd-cell-item>
      </yd-cell-group>
      <yd-cityselect v-model="show1" :callback="result1" :items="district"></yd-cityselect>

      <yd-accordion>
        <yd-accordion-item title="">
          <div style="padding: .24rem;">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </yd-accordion-item>
        <yd-accordion-item title="">
          <div style="padding: .24rem;">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </yd-accordion-item>
        <yd-accordion-item title="">
          <div style="padding: .24rem;">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </yd-accordion-item>
      </yd-accordion>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import District from 'ydui-district/dist/jd_province_city_area_id';
export default {
  name: "statistical",
  data () {
    return {
      show1: false,
      model1: '',
      district: District
    }
  },
  methods: {
    result1 (ret) {
      console.log(ret.itemName1)
      this.model1 = ret.itemName1 + ' ' + ret.itemName2 + ' ' + ret.itemName3;
    }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
</style>



